@import "../../global.scss";

.works{
    background-color: crimson;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    .arrow{
        height: 50px;
        position: absolute;
        cursor: pointer;
       @include mobile{
        display: none;
       }
       

        &.left{
            left: 100px;
            transform: rotate(90deg);
           
        }
        &.right{
            right: 100px;
            transform: rotate(-90deg);
        }
    }

    .slider{
        height: 350px;
        display: flex;
        position: absolute;
        left: 0;
        transition: all 1s ease-out;

        @include mobile{
            height: 100vh;
            flex-direction: column;
            justify-content: center;
        }

        .container{
            width: 100vw;
            display: flex;
            align-items: center;
            justify-content: center;

            .items{
                width: 700px;
                height: 100%;
                background-color: white;
                border-radius: 20px;
                display: flex;
                align-items: center;
                justify-content: center;

                @include mobile{
                    width: 90%;
                    height: 180px;
                    margin: 5px 0;
                }

                .left{
                    flex: 4;
                    height: 80%;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    .leftContainer{
                        width: 90%;
                        height: 70%;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;

                        .imgContainer{
                            width: 40px;
                            height: 40px;
                            border-radius: 50%;
                            background-color: rgb(233, 158, 130);
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            img{
                                width: 25px;
                            }
                        }

                        h2{
                            font-size: 20px;
                            @include mobile{
                                font-size: 10px;
                            }
                        }
                        p{
                            font-size: 13px;
                            @include mobile{
                                font-size: 8px;
                            }
                        }
                        span{
                            font-size: 12px;
                            font-weight: 600;
                            text-decoration: underline;
                            cursor: pointer;
                        }

                    }

                }
                .right{
                    flex: 8;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    overflow: hidden;

                    img{
                        width: 500px;
                        transform: rotate(-10deg);

                    }
                }
            }
        }
    }
}